
import Tabbar from '@/components/Public/Tabbar'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
Tabbar, Swiper, Cell, Group, Radio, Badge, Flexbox, FlexboxItem, Divider
},
data: () => ({
list: [],
noMore: false,
isEmpty: false,
page: 1,
pageSize: 20,
img_list: []
}),
mounted () {
this.getSwiper()
this.getData()
},
methods: {
loadMore: function () {
if (this.list.length === 0) {
return
}
if (this.noMore) {
return
}
this.busy = true
setTimeout(() => {
this.getData()
this.busy = false
}, 1500)
},
getSwiper: function () {
this.$getData(this.$configs.api.swiper, '/5addfa73fe04b53170f7fe42', response => {
const list = response.list
list.forEach(item => {
this.img_list.push({ img: item.src })
})
})
},
getData: function () {
this.$getData(this.$configs.api.article, `?page=${this.page}&pageSize=${this.pageSize}`, response => {
const list = response.list
if (JSON.stringify(list) === '[]') {
if (this.list.length === 0) {
this.isEmpty = true
return
} else {
this.noMore = true
return
}
} else {
this.list = this.list.concat(list)
this.page++
}
})
}
}
}



